___CSS_LOADER_EXPORT___.push([module.id, `.gallery__imageContainer {
  cursor: grab;
  overflow: hidden;
}
.gallery__imageContainer #image-track {
  display: flex;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(0%, -50%);
  user-select: none;
  /* -- Prevent image highlighting -- */
}
.gallery__imageContainer #image-track .imageContainer {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.gallery__imageContainer #image-track .imageContainer .image {
  width: 37vmin;
  height: 55vmin;
  margin-left: 2vmin;
  margin-right: 2vmin;
  opacity: 1;
  object-fit: cover;
  object-position: 100% center;
  transition: width 0.6s ease, opacity 0.6s ease, margin-left 0.6s ease, margin-right 0.6s ease;
}
.gallery__imageContainer #image-track .imageContainer .imageLoading {
  color: white;
  width: 37vmin;
  height: 55vmin;
  margin-left: 2vmin;
  margin-right: 2vmin;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid rgba(255, 255, 255, 0.2);
}
.gallery__imageContainer #image-track .imageContainer .imageSensible {
  width: 0;
  opacity: 0;
  margin-left: 0;
  margin-right: 0;
}
.gallery__imageDescription {
  width: 100vw;
  position: absolute;
  right: 0;
  bottom: 5vh;
  text-align: center;
  font-size: 13px;
  font-weight: 300;
  letter-spacing: 5px;
}
.gallery__imageDescription .imageDescriptionSeparator {
  opacity: 0.2;
}
.gallery__imageDescription .imageDescriptionInfoOpen {
  opacity: 0.2;
  padding-top: 10px;
  letter-spacing: 2px;
}
