  width: 100vw;
  position: absolute;
  right: 0;
  top: 7vh;
  text-align: center;
  font-size: 22px;
  font-weight: 300;
  letter-spacing: 5px;
  opacity: 1;
  color: black;
  transition: opacity 0.3s ease-in-out, color 0.3s linear;
}
.mainTitle .mainTitleL {
  visibility: visible;
}
.mainTitle .mainTitleK {
  vertical-align: middle;
  letter-spacing: 5px;
  font-size: 52px;
  font-family: 'Monsieur La Doulaise', cursive;
}
.mainTitle .mainSubTitle {
  position: relative;
  top: -30px;
  left: 60px;
  font-size: 10px;
  font-weight: 300;
  letter-spacing: 7.5px;
  opacity: 0.5;
}
.mainTitleGone {
  opacity: 0;
}
