___CSS_LOADER_EXPORT___.push([module.id, `.galleryMobile__container {
  position: relative;
  z-index: 1;
  width: 100vw;
  min-height: 100vh;
  padding: 10vh 0 10px 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  font-weight: 300;
  letter-spacing: 2px;
}
.galleryMobile__container .galleryMobile__imageContainer .galleryMobile__imageLoading {
  width: 100vw;
  height: 100vw;
  color: white;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid rgba(255, 255, 255, 0.2);
}
.galleryMobile__container .galleryMobile__imageContainer .galleryMobile__image {
  width: calc(100vw - 30px);
  height: 100vw;
  margin: 8px;
  opacity: 1;
  object-fit: cover;
  object-position: 100% center;
  transition: height 0.6s ease, opacity 0.6s ease, margin-left 0.6s ease, margin-right 0.6s ease;
}
.galleryMobile__container .galleryMobile__imageContainer .galleryMobile__imageSensible {
  height: 0;
  opacity: 0;
  margin: 0;
}
