.mainContainer {
  width: 100vw;
  height: 100vh;
  background-color: transparent;
  transition: background-color 0.3s ease-in-out;
}
.mainContainer .noisyBackGround {
  position: fixed;
  top: -50%;
  left: -50%;
  right: -50%;
  bottom: -50%;
  width: 200%;
  height: 200vh;
  background: transparent url(${___CSS_LOADER_URL_REPLACEMENT_0___});
